.chat {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
}
.chatMain {
    background: #fff;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex: 0.8;
}
@media screen and (max-width: 500px) {
    .chatMain {
        flex: 1;
    }
}
