.homeForm {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.homeForm > * {
    margin-bottom: 10px;
}
.homeHeader {
    margin-bottom: 30px;
}
.usernameInput {
    padding: 10px;
    width: 50%;
}
.submitBtn {
    width: 200px;
    padding: 10px;
    font-size: 16px;
    cursor: pointer;
    background-color: #607eaa;
    color: #f9f5eb;
    outline: none;
    border: none;
    border-radius: 5px;
}